import React from 'react'
import styled from 'styled-components'
import Container from '../utils/Container';
import Image from '../../assets/tingey-injury-law-firm-6sl88x150Xs-unsplash.jpg'

export default function Law() {
    return (
        <StyledWrapper>
            <main>
                <div id="image"><h2>Rechtliches</h2></div>
                <Container>
                    {/* <div className="images">
                        <img src={LawImage} alt="EU Law" />
                        <img src={EU_FlagImage} alt="EU Flag" />
                    </div> */}
                    {/* <h2 className="color">Rechtliches</h2> */}
                    <article>
                        <p className='color'><b>Fr einen rechtsgltigen EU-Fhrerschein mssen Sie zwei 
                            Voraussetzungen erfllen:</b>
                        </p>
                        <p>Am 26.04.2012 wurde vom Europischen Gerichtshof (EuGh) verabschiedet, 
                            dass alle EU-Staaten die Fahrerlaubnis anderer EU-Staaten ohne zustzliche 
                            Formalitten anerkennen mssen, auch wenn in Deutschland eine MPU-Auflage besteht. 
                            Auch die am 19.01.2013 in Kraft getretene dritte europische Fhrerscheinrichtlinie 
                            hat in Bezug auf die gegenseitige Anerkennung der Fhrerscheine innerhalb Europas 
                            nichts gendert.</p>
                        <p>Es darf keine Fahrsperre vorliegen. Sollte Ihnen der Fhrerschein entzogen worden 
                            sein und Ihnen wurde fr eine gewisse Zeit verboten Ihre Fahrerlaubnis neu zu erwerben, 
                            so drfen Sie auch im europischen Ausland keinen neuen Fhrerschein machen. Sollten Sie 
                            die Sperrfrist nicht einhalten, und trotzdem den Fhrerschein machen, so ist dieser 
                            definitiv ungltig, und zwar in ganz Europa.</p>
                        <p>Der Fhrerscheininhaber muss die Fahrerlaubnis whrend eines mindestens 185-tgigen 
                            zusammenhngenden Aufenthaltes im EU-Mitgliedsstaat erwerben. Die Wohnsitzregelung ist 
                            eine zentrale Vorschrift fr die Erteilung eines EU-Fhrerscheines. Fr die Berechnung 
                            der 185-Tage-Frist ist zunchst entscheidend, dass die Person zum Wohnen berechtigt ist.</p>
                    </article>
                    <article>
                        <p className='color'><b>185-Tage-Regelung</b></p> 
                        <p>Die wichtigste Voraussetzung fr den EU-Fhrerschein.</p>
                        <p>Der Erwerb eines EU-Fhrerscheins setzt nach EU-Richtlinien voraus, dass Sie fr 
                            mindestens 185 Tage in dem betreffenden Land angemeldet sind. Der EuGH hat am 29.04.2004 
                            folgendes Urteil (Aktenzeichen C-476/01) erlassen: Ein in einem EU-Mitgliedsstaat nach 
                            Ablauf der Sperrfrist (z.B. Fahrverbot) erworbener Fhrerschein muss von deutschen 
                            Behrden anerkannt werden. Die Anerkennung darf nicht mit der Begrndung abgelehnt 
                            werden, dass der Inhaber in einem anderen EU-Mitgliedsstaat seinen Fhrerschein erworben hat, 
                            in dem er nie einen Wohnsitz hatte. Die Entscheidung des EuGH wird damit begrndet, 
                            dass die berprfung der 185-Tage-Wohnsitzregelung alleinige Sache des Ausstellerstaates ist, 
                            da in jedem anderen Fall die Integritt des Staates verletzt wird. Es ist jedoch nicht ntig, 
                            185 Tage auf den Fhrerschein zu warten, wenn Sie den EU-Fhrerschein in Polen machen. 
                            Sie mssen nur die Anmeldung fr mind. 185 Tage besitzen. Nach Ihrer bestandenen Prfung 
                            erhalten Sie bereits ca. drei Wochen spter Ihren rechtsgltigen 
                            EU-Fhrerschein ausgehndigt!</p>
                        <p>Sollte die Gltigkeit Ihres EU-Fhrerscheins in einer Verkehrskontrolle dann doch 
                            einmal angezweifelt werden, besteht kein Grund zur Sorge, wenn Sie die o.g. Punkte 
                            eingehalten worden sind. Das EuGH entschied, dass die Prfung der Erfllung des 
                            Wohnsitzprinzips allein auf Seiten des Landes liegt, das den EU-Fhrerschein ausstellt. 
                            Das heit, dass bereits die Erteilung des EU-Fhrerscheins in Polen sowie das Fehlen 
                            gegenlufiger Angaben aus dem Ausstellerstaat beweisen, dass die Wohnsitzerfordernisse 
                            beim Erwerb beachtet wurden.</p>
                    </article>
                    <article>
                        <p className='color'><b>Mehrere Wohnsitze sind erlaubt.</b></p>
                        <p>Wichtig und praxisrelevant ist darber hinaus, dass kein Wohnsitzversto vorliegt, 
                            wenn whrend des betreffenden Zeitraumes ein Wohnsitz in Deutschland beibehalten wurde. 
                            Ein Urteil des Europischen Gerichtshofs (EuGH) besttigte, dass eine einwohnermeldeamtliche 
                            Meldung in Deutschland (im Zeitraum des Fhrerscheinerwerbs) nicht gegenlufig als Nachweis 
                            eines Wohnsitzverstoes herangezogen werden kann (vgl. u.a. OVG Nordrhein-Westfalen, Beschluss 
                            vom 19.12.13, A.Z. 16 B 1287/13 RN 30; EuGH, Urteil v. 26.6.08; C-329/06 und C-343/06 
                            (Wiedemann u.a.) und C-334/06 bis C-336/06 (Zerche u.a.). Folglich liegt trotz 
                            Wohnsitzmeldung in Deutschland eine EU-Fhrerschein-Gltigkeit der im Ausland erworbenen 
                            Fahrerlaubnis vor.</p> 
                        <p>Staatsanwaltschaft muss Wohnsitzversto belegen.</p>
                        <p>Fr etwaige Gerichtsverhandlungen ist daher zu beachten, dass Betroffener keinerlei 
                            uerungen zur Einhaltung des Wohnsitzerfordernisses treffen sollten. 
                            Die Staatsanwaltschaft bzw. Verwaltungsbehrde msste aufgrund unbestreitbarer 
                            Tatsachen aus dem Ausstellerstaat den Nachweis erbringen, dass der Wohnsitz nicht 
                            im Ausstellerstaat bestanden hat, bevor es zum Erwerb der Fahrerlaubnis kam. 
                            Lediglich wenn eine Mitteilung des Ausstellermitgliedsstaates vorliegt, nach der 
                            sich der deutsche Staatsangehrige im zeitlichen Zusammenhang mit der Erlangung des 
                            EU-Fhrerscheins nicht mindestens 185 Tage im Ausstellermitgliedsstaat aufgehalten hat, 
                            muss die Fahrerlaubnis nicht anerkannt werden. Erst mit dem eindeutigen Hinweis durch den 
                            Fhrerschein-Aussteller-Staat, dass gegen die 185-Tage-Regelung verstoen wurde, kann die 
                            Gltigkeit des Fhrerscheins angezweifelt werden.</p>
                        <p>Prinzipiell muss sich der Versto entweder aus dem Fhrerschein selbst ergeben 
                            oder aus anderen, vom Ausstellermitgliedstaat herrhrenden, unbestreitbaren Informationen. 
                            Hintergrund dieser eingeschrnkten Definition ist vermutlich, dass der EuGH eine Ausnahme vom 
                            gegenseitigen Anerkennungsgrundsatz fr EU-Fhrerscheine nur in dem Fall machen will, wenn der 
                            Ausstellermitgliedsstaat selbst einrumt, bei der Fhrerschein-Ausstellung die 
                            Wohnsitzvoraussetzung nicht beachtet zu haben. In smtlichen anderen Fllen gilt die 
                            uneingeschrnkte Gltigkeit des Fhrerscheins auch in Deutschland.</p>
                    </article>
                </Container>
            </main>
        </StyledWrapper>
    )
}
const StyledWrapper = styled.section`
    margin-top: 6rem;
    padding-bottom: 4rem;
    #image{
        position: relative;
        background-image: url(${Image});
        background-size: cover;
        background-position: center;
        width: 100%;
        height: 400px;
        display: grid;
        place-content: center;
        font-size: 2rem;
        text-transform: uppercase;
        color: white;
        &::after{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(0,0,0,.6);
        }
        h2{
            position: relative;
            z-index: 1;
        }
    }
    .images{
        display: flex;
        justify-content: space-evenly;
        height: 200px;
    }
    p{
        margin-top: 1rem;
    }
    article{
        margin-top: 3rem;
    }
`;