import { keyframes } from "styled-components";
import { createGlobalStyle } from "styled-components";
import { MAX_WIDTH } from "../constants";

const spin = keyframes`
    to{
        transform: rotate(720deg);
    }
`;
export const GlobalStyle = createGlobalStyle`
    *, *::before, *::after{
        box-sizing: border-box;
        margin: 0;
        padding: 0;
        border-radius: 0;
    }
    html, body{
        font-family: 'Inter', sans-serif;
        font-size: 16px;
        scroll-behavior: smooth;
        line-height: 1.5;
        overflow-x: hidden;
    }
    a{
        color: inherit;
        text-decoration: none;
        outline: none;
    }
    img{
        display: block;
    }
    .container{
        max-width: ${MAX_WIDTH};
        margin: 0 auto;
    }
    .btn{
        display: flex;
        gap: 0.5rem;
        align-items: center;
        justify-content: center;
        padding: 1rem 2rem;
        background-color: #5A25A5;
        transition: background-color .2s ease;
        cursor: pointer;
        color: white;
        font-weight: bold;
        border-radius: 0.5rem;
        outline: none;
        border: 2px solid transparent;
        &:hover{
            background-color: #9139D2;
        }
        &:focus{
            background-color: #9139D2;
            color: white;
        }
        &-nav{
            padding: 0.5rem 2rem;
        }
        &-white{
            background-color: white;
            color: #5A25A5;
            &:hover{
                background-color: #e0e0e0;
            }
            &:focus{
                background-color: white;
                border-color: black;
            }
        }
        &-outline{
            background-color: transparent;
            color: #5A25A5;
            border-color: #5A25A5;
            cursor: auto;
            &:hover{
                background-color: transparent;
            }
        }
        svg{
            font-size: 1.5rem;
            &.loading{
                animation: ${spin} 2s ease infinite;
            }
        }
    }
    button, input, textarea{
        font-size: inherit;
        font-family: inherit;
        border: 0;
        width: 100%;
    }
    .gray{
        color: #666666;
    }
    h2.color{
        text-transform: uppercase;
        color: #9139D2;
    }
    .color{
        color: #9139D2;
    }
    main{
        min-height: 100vh;
    }
`;