import React, { useEffect, useState } from 'react'
import LOGO from '../../../assets/LOGO.png'
import Container from '../../utils/Container'
import { StyledNav } from './Nav.styles';
import { Icon } from '@iconify/react';
import Scroll, { Link } from "react-scroll";
import { Link as RouterLink, useLocation } from 'react-router-dom'
import TopBar from '../../molecules/TopBar';
import useWindowDimensions from '../../../hooks/getWindowDimensions'

export default function Nav({contact}) {
    const [isNavOpen, setNavOpen] = useState(false);
    const [isTopBarShown, setTopBarShown] = useState(true);
    const { width: viewportWidth } = useWindowDimensions();
    const location = useLocation();
    const handleScroll = () => {
        if(window.pageYOffset > 100) setTopBarShown(false)
        else setTopBarShown(true)
    };
    useEffect(() => {
        window.addEventListener("scroll", handleScroll);
        return () => {
          window.removeEventListener("scroll", handleScroll);
        };
    }, []);
    const closeNav = ()=>{
        setNavOpen(false)
    }
    const handleScrollToTop = ()=>{
        Scroll.animateScroll.scrollToTop()
    }
    return (
        <>
            <StyledNav>
                <TopBar contact={contact} className={isTopBarShown ? `` : `hide`} />
                <Container className='nav'>
                    <RouterLink to='/' id="logo" onClick={()=>handleScrollToTop()}>
                        <img src={LOGO} alt="Logo" />
                    </RouterLink>
                    <ul className={isNavOpen ? `menu active` : `menu`}>
                        <Icon id='quit-btn' icon="eva:close-outline" onClick={()=>setNavOpen(false)} tabIndex='0' />
                        <li>
                            <RouterLink to='/' tabIndex='0' onClick={()=>{handleScrollToTop(); closeNav()}}>
                                Start
                            </RouterLink>
                        </li>
                        {location.pathname === "/" && (
                            <>
                                <li>
                                    <Link
                                        activeClass="active"
                                        to="ablauf"
                                        spy={true}
                                        smooth={true}
                                        offset={-70}
                                        duration={500}
                                        tabIndex='0'
                                        delay={viewportWidth <= 1000 ? 400 : 0}
                                        onClick={closeNav}
                                    >Ablauf</Link>
                                </li>
                                <li>
                                    <Link
                                        activeClass="active"
                                        to="OhneMPU"
                                        spy={true}
                                        smooth={true}
                                        offset={-70}
                                        duration={500}
                                        tabIndex='0'
                                        delay={viewportWidth <= 1000 ? 400 : 0}
                                        onClick={closeNav}
                                    >Ohne MPU</Link>
                                </li>
                                <li>
                                    <Link
                                        activeClass="active"
                                        to="pricelist"
                                        spy={true}
                                        smooth={true}
                                        duration={500}
                                        tabIndex='0'
                                        delay={viewportWidth <= 1000 ? 400 : 0}
                                        onClick={closeNav}
                                    >Preisliste</Link>
                                </li>
                            </>
                        )}
                        <li>
                            <RouterLink to='/galerie' onClick={()=>{handleScrollToTop(); closeNav()}}>
                                Galerie
                            </RouterLink>
                        </li>
                        <li>
                            <RouterLink to='/rechtliches' onClick={()=>{handleScrollToTop(); closeNav()}}>
                                Rechtliches
                            </RouterLink>
                        </li>
                        {location.pathname === "/" && (
                            <li>
                                <Link
                                    activeClass="none"
                                    className='btn btn-nav'
                                    to="kontakt"
                                    spy={true}
                                    smooth={true}
                                    duration={500}
                                    tabIndex='0'
                                    delay={viewportWidth <= 1000 ? 400 : 0}
                                    onClick={closeNav}
                                >Kontakt</Link>
                            </li>
                        )}
                    </ul>
                    <div id="hamburger" onClick={()=>setNavOpen(true)}>
                        <Icon icon="eva:menu-2-outline" />
                    </div>
                </Container>
            </StyledNav>
        </>
    )
}