import styled from 'styled-components'
import { CUBIC_BEZIER } from '../../../constants';

export const StyledNav = styled.nav`
    position: fixed;
    top: 0;
    z-index: 3;
    width: 100%;
    background-color: white;
    .nav{
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 100%;
        height: 60px;
    }
    #logo{
        width: 200px;
        cursor: pointer;
        img{
            width: 100%;
        }
    }
    .menu{
        position: relative;
        list-style: none;
        display: flex;
        align-items: center;
        gap: 2rem;
        transition: transform .4s ${CUBIC_BEZIER};
        &.active{
            transform: translateX(0);
            pointer-events: all;
        }
        #quit-btn{
            position: absolute;
            top: 1rem;
            right: 2rem;
            font-size: 2rem;
            cursor: pointer;
            display: none;
        }
        li{
            cursor: pointer;
            transition: color .2s ease;
            &:hover{
                color: #5A25A5;
            }
        }
        a{
            position: relative;
            display: inline-block;
            &::after{
                content: '';
                position: absolute;
                bottom: 0;
                left: 0;
                width: 0;
                height: 2px;
                background-color: #5A25A5;
                transition: width .2s ${CUBIC_BEZIER};
            }
            &.active{
                color: #5A25A5;
                &::after{
                    width: 100%;
                }
            }
            &:not(.btn):focus{
                color: #5A25A5;
            }
        }
        @media screen and (max-width: 1100px) {
            position: absolute;
            top: 0;
            right: 0;
            z-index: 10;
            max-width: 300px;
            width: 100%;
            padding: 4rem 2rem 2rem;
            background-color: white;
            flex-direction: column;
            border-radius: .5rem 0 0 .5rem;
            box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
            transform: translateX(110%);
            pointer-events: none;
            #quit-btn{
                display: block;
            }
        }
    }
    #hamburger{
        font-size: 2rem;
        place-content: center;
        cursor: pointer;
        display: none;
        @media screen and (max-width: 1100px) {
            display: grid;
        }
    }
`;