import { Icon } from '@iconify/react'
import React from 'react'
import styled from 'styled-components'
import Container from '../utils/Container'

const SingleIcon = ({icon, color, text, delay})=>{
    return (
        <StyledIcon data-aos="fade-up" data-aos-delay={delay}>
            <div className="circle" style={{backgroundColor: color}}><Icon icon={icon} /></div>
            <p>{text}</p>
        </StyledIcon>
    )
}

export default function Icons({data}) {
    const prosArray = data.split(';');
    return (
        <StyledContainer>
            <SingleIcon delay="400" icon='eva:swap-outline' color='#5A25A5' text={prosArray[0]} />
            <SingleIcon delay="500" icon='eva:shield-outline' color='#9139D2' text={prosArray[1]} />
            <SingleIcon delay="600" icon='eva:home-outline' color='#D07F8E' text={prosArray[2]} />
            <SingleIcon delay="700" icon='eva:globe-2-outline' color='#7F82D0' text={prosArray[3]} />
            <SingleIcon delay="800" icon='eva:checkmark-square-outline' color='#B455A7' text={prosArray[4]} />
            <SingleIcon delay="900" icon='eva:shopping-cart-outline' color='#5B5FBE' text={prosArray[5]} />
        </StyledContainer>
    )
}
const StyledIcon = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 150px;
    text-align: center;
    .circle{
        display: grid;
        place-content: center;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        font-size: 2rem;
        color: white;
    }
    p{
        margin-top: 1rem;
    }
`;
const StyledContainer = styled(Container)`
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
    padding-top: 4rem;
`;