import { ReactNotifications } from "react-notifications-component";
import Icons from "./components/molecules/Icons";
import MPU from "./components/molecules/MPU";
import Process from "./components/molecules/Process";
import Hero from "./components/organisms/hero/Hero";
import Kontakt from "./components/organisms/kontakt/Kontakt";
import Nav from "./components/organisms/nav/Nav";
import Waves from "./components/utils/Waves";
import 'react-notifications-component/dist/theme.css'
import AOS from "aos";
import 'aos/dist/aos.css'
import PriceList from "./components/molecules/PriceList";
import Footer from "./components/molecules/Footer";
import { useEffect, useState } from "react";
import { useQuery } from '@apollo/client'
import { MASS_QUERY } from './Queries'
import Loader from "./components/utils/Loader";
import { BrowserRouter, Routes, Route } from 'react-router-dom'
import Galerie from "./components/organisms/galerie/Galerie";
import Service from "./components/molecules/Service";
import Law from "./components/molecules/Law";

AOS.init({
  offset: 200,
  duration: 400,
  easing: 'ease',
  delay: 400,
  anchorPlacement: 'center-bottom'
});

function App() {
  const {loading, error, data} = useQuery(MASS_QUERY)
  const [newData, setNewData] = useState({
    contact: {},
    hero: {},
    advantages: "",
    process: {},
    priceList: [],
    ohneMpu: "",
    copyright: 0,
    service: ""
  })
  
  useEffect(()=>{
    if(data){
      setNewData({
        contact: data.contact,
        hero: data.heading,
        advantages: data.advantages.advantages.json.content[0].content[0].value,
        process: data.process,
        priceList: data.priceListCollection.items,
        ohneMpu: data.ohneMpu,
        copyright: data.copyright,
        service: data.service
      })
    }
  }, [data])

  if(loading || !data) return <Loader />
  if(error) return <Loader error={true} />
  return (
    <BrowserRouter>
      <ReactNotifications />
      <Nav contact={newData.contact} />
      <Routes>
        <Route path="/" element={<Homepage newData={newData} />} />
        <Route path="/galerie" element={<Galerie />} />
        <Route path="/rechtliches" element={<Law />} />
      </Routes>
      <Footer copyright={newData.copyright} />
    </BrowserRouter>
  );
}

export default App;

const Homepage = ({newData}) => (
    <main>
      <Hero data={newData.hero} />
      <Waves />
      <div style={{backgroundColor: '#F0EFF5'}}>
        <div id="ablauf">
          <Icons data={newData.advantages} />
          <Process data={newData.process} />
          <Service data={newData.service.service} />
        </div>
        <MPU data={newData.ohneMpu.ohneMpu} />
        <PriceList data={newData.priceList} />
      </div>
      <Kontakt contact={newData.contact} />
    </main>
)