import React from 'react'
import styled from 'styled-components'
import Container from '../utils/Container'

const Item = ({category, price, delay}) => {
    return (
        <StyledItem data-aos='fade-up' data-aos-delay={delay}>
            <p className="category">{category}</p>
            <p className="price">{price}</p>
        </StyledItem>
    )
}
export default function PriceList({data}) {
    return (
        <section id="pricelist">
            <StyledContainer>
                <h2 className="color" data-aos="fade-right">Preisliste</h2>
                <div className="wrapper">
                    {[...data].reverse().map((item, index) => (
                        <Item key={index} category={item.category} price={item.price} delay={index === 0 ? '400' : 400+index*100} />
                    ))}
                </div>
            </StyledContainer>
        </section>
    )
}
const StyledContainer = styled(Container)`
    padding-top: 6rem;
    padding-bottom: 6rem;
    .wrapper{
        margin-top: 4rem;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-evenly;
        gap: 2rem;
    }
`;
const StyledItem = styled.div`
    min-width: 300px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    gap: 2rem;
    padding: 2rem;
    background-color: white;
    border-radius: 1rem;
    .category{
        font-size: 1.5rem;
        font-weight: bold;
    }
    .price{
        background-color: #5A25A5;
        color: white;
        padding: 1rem 2rem;
        border-radius: 1rem;
        font-weight: bold;
    }
`;