import React, { useEffect, useState } from 'react'
import Link from 'react-scroll/modules/components/Link';
import styled from 'styled-components'
import Container from '../../utils/Container';
import Image from '../../../assets/gallery/DSC_9822.jpg'

export default function Hero({data}) {
    const [description, setDescription] = useState("");
    const [prosList, setProsList] = useState([]);
    useEffect(()=>{
        if(data && data.description && data.pros){
            setDescription(data.description.json.content[0].content[0].value.replace('\n', '<br />'))
            setProsList(data.pros.json.content[0].content)
        }
    }, [data])
    return (
        <StyledHero id='start'>
            <Container>
                <div className="left">
                    <p className='color' data-aos="fade-down" data-aos-delay='1500'>Mehr als 30 jahre erfahrung</p>
                    <h1 data-aos="fade-right">{data.title}</h1>
                    <p className="gray" data-aos="fade-right" data-aos-delay="500" dangerouslySetInnerHTML={{__html: description}}></p>
                    <Link
                        data-aos="fade-right" data-aos-delay="800"
                        to="ablauf"
                        className='btn'
                        spy={true}
                        smooth={true}
                        offset={-70}
                        tabIndex='0'
                        duration={500}>
                        {data.buttonText}
                    </Link>
                </div>
                <div className="right" data-aos="fade-up">
                    <img src={Image} alt="Fahrschule" />
                    <div className="text">
                        <h2 data-aos="fade-down" data-aos-delay='600'>Warum bei uns?</h2>
                        <ul data-aos="fade-left" data-aos-delay='800'>
                            {prosList.map((item, index) => {
                                return (
                                    <li key={index}>{item.content[0].content[0].value}</li>
                                )
                            })}
                        </ul>
                    </div>
                </div>
            </Container>
        </StyledHero>
    )
}
const StyledHero = styled.section`
    padding-top: 4rem;
    margin-top: 5rem;
    .container{
        display: flex;
        flex-wrap: wrap;
        gap: 2rem;
        justify-content: center;
    }
    p.color{
        display: inline-block;
        border-bottom: 2px solid #9139D2;
    }
    .left{
        max-width: 500px;
        width: 100%;
        p{
            margin-top: 1rem;
        }
        .btn{
            display: inline-block;
            margin-top: 2rem;
        }
    }
    .right{
        position: relative;
        width: 100%;
        max-width: 500px;
        border-radius: 1rem;
        display: grid;
        place-content: center;
        overflow: hidden;
        &::before{
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba(90, 37, 165, .8);
            z-index: 1;
        }
        img{
            position: absolute;
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }
        .text{
            position: relative;
            z-index: 1;
            padding: 2rem;
            color: white;
            p{
                margin-top: 2rem;
            }
        }
        li{
            margin-left: 1rem;
        }
    }
    h1{
        font-size: 3rem;
    }
`;