import { Icon } from '@iconify/react';
import React, { useState } from 'react'
import styled from 'styled-components';
import Container from '../../utils/Container';

export default function Galerie() {
    const [selectedImage, setSelectedImage] = useState(null);
    function importAll(r) {
        return r.keys().map(r);
    }
    const images = importAll(require.context('../../../assets/gallery', false, /\.(png|jpe?g|svg)$/));
    const handleClick = (image)=>{
        setSelectedImage(image);
    }
    return (
        <main>
            {selectedImage && (
                <BigImageWrapper url={selectedImage}>
                    <div className="overlay" onClick={()=>setSelectedImage(null)}></div>
                    <div className="image">
                        <div className="quit" onClick={()=>setSelectedImage(null)}><Icon icon="eva:close-outline" /></div>
                    </div>
                </BigImageWrapper>
            )}
            <Container>
                <StyledList>
                    {images.map((image, index) => (
                        <li key={index} onClick={()=>handleClick(image)}>
                            <StyledImage url={image} />
                        </li>
                    ))}
                </StyledList>
            </Container>
        </main>
    )
}
const StyledImage = styled.div`
    width: 300px;
    aspect-ratio: 16 / 9;
    background-image: url(${({url}) => url});
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
`;
const StyledList = styled.ul`
    margin-top: 8rem;
    padding: 2rem;
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    list-style: none;
    align-items: center;
    justify-content: center;
    li{
        border-radius: 1rem;
        overflow: hidden;
        cursor: pointer;
    }
`;
const BigImageWrapper = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 15;
    background-color: rgba(0,0,0,.8);
    .overlay{
        width: 100%;
        height: 100vh;
    }
    .image{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        max-width: 900px;
        width: 100%;
        aspect-ratio: 16 / 9;
        background-image: url(${({url}) => url});
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        border-radius: 1rem;
    }
    .quit{
        position: absolute;
        top: 1rem;
        right: 1rem;
        display: grid;
        place-content: center;
        padding: 0.3rem;
        font-size: 2rem;
        background-color: rgba(0,0,0,.8);
        border-radius: 0.5rem;
        cursor: pointer;
        color: white;
    }
`;