import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import Container from '../utils/Container';

const Item = ({number, children}) => {
    return (
        <StyledItem>
            <span className="number" data-aos="fade-up" data-aos-delay="600">{number}.</span>
            <div data-aos="fade-up" data-aos-delay="800">{children}</div>
        </StyledItem>
    )
}

export default function Process({data}) {
    const [process, setProcess] = useState({
        step1: {},
        step2: {},
        step3: {},
    });
    useEffect(()=>{
        if(data && data.step1 && data.step2 && data.step3) setProcess({
            step1: data.step1.split("\n"),
            step2: data.step2.split("\n"),
            step3: data.step3.split("\n"),
        })
    }, [data])
    if(!Object.keys(process.step1).length || !Object.keys(process.step2).length || !Object.keys(process.step3).length) return null;
    return (
        <Container>
            <StyledProcess>
                <div className="items">
                    <Item number={1}>
                        <p className="title">{process.step1[0].replace("###", "").trim()}</p>
                        <ul>
                            {process.step1.map((item, index) => (
                                index > 1 && <li key={index}>{item.replace("-", "").trimStart()}</li>
                            ))}
                        </ul>
                    </Item>
                    <Item number={2}>
                        <p className="title">Erste Anreise</p>
                        <ul>
                            {process.step2.map((item, index) => (
                                index > 1 && <li key={index}>{item.replace("-", "").trimStart()}</li>
                            ))}
                        </ul>
                    </Item>
                    <Item number={3}>
                        <p className="title">Zweite Anreise</p>
                        <ul>
                            {process.step3.map((item, index) => (
                                index > 1 && <li key={index}>{item.replace("-", "").trimStart()}</li>
                            ))}
                        </ul>
                    </Item>
                </div>
            </StyledProcess>
        </Container>
    )
}
const StyledProcess = styled.section`
    margin-top: 6rem;
    .items{
        margin-top: 4rem;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        gap: 2rem;
    }
`;
const StyledItem = styled.div`
    display: flex;
    gap: 1rem;
    width: 350px;
    li{
        margin-left: 1rem;
    }
    .number{
        font-size: 4rem;
        font-weight: bold;
        color: #9139D2;
    }
    .title{
        font-weight: bold;
        font-size: 1.5rem;
    }
`;