import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import Container from '../utils/Container'

export default function MPU({data}) {
  const [message, setMessage] = useState("");
  const [transformedMessage, setTransformedMessage] = useState("");

  useEffect(() => {
    if(data) setMessage(data.replaceAll('\n', "<br />"));
  }, [data]);

  useEffect(()=>{
    let array = message.split("__");
    for(let i = 0; i<array.length; i++){
      if(i %2 !== 0){
        array[i] = `<b>${array[i]}</b>`
      }
    }
    setTransformedMessage(array.toString().replaceAll(",<b>", "<b>").replaceAll("</b>,", "</b>"))
  }, [message])
  return (
    <StyledContainer id='OhneMPU'>
      <div className='wrapper'>
        <h2 className="color" data-aos="fade-right">Ohne MPU</h2>
        <div className='pros'>
          <div className="btn btn-outline" data-aos="fade-right" data-aos-delay="500">100% Legal</div>
          <div className="btn btn-outline" data-aos="fade-right" data-aos-delay="600">Preisgarantie</div>
        </div>
        <p data-aos="fade-up" data-aos-delay="800" dangerouslySetInnerHTML={{__html: transformedMessage}}></p></div>
    </StyledContainer>
  )
}
const StyledContainer = styled(Container)`
    margin-top: 6rem;
    .wrapper{
      margin-top: 6rem;
    }
    p{
      margin-top: 4rem;
    }
    .pros{
      display: flex;
      flex-wrap: wrap;
      gap: 1rem;
      margin-top: 4rem;
    }
`;