import { Icon } from '@iconify/react';
import React from 'react';
import styled from 'styled-components';
import Container from '../utils/Container';
import BG from '../../assets/mesh.png'
import { CUBIC_BEZIER } from '../../constants';

const TopBar = ({className, contact}) => {
    return (
        <StyledTopContact className={className}>
            <Container className="contact">
                <div>
                    <p><a href={`tel:${contact.phoneNumberDe}`}><Icon icon="eva:phone-outline" />{contact.phoneNumberDe} (DE)</a></p>
                    <p>/ <a href={`tel:${contact.phoneNumber}`}>{contact.phoneNumber} (PL)</a></p>
                </div>
                <p className='email'><a href={`mailto:${contact.email}`}><Icon icon="eva:email-outline" />{contact.email}</a></p>
            </Container>
        </StyledTopContact>
    );
}

export default TopBar;
const StyledTopContact = styled.div`
    height: 44px;
    background-image: url(${BG});
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
    transition: height .4s ${CUBIC_BEZIER};
    p{
        display: flex;
        gap: 0.2rem;
    }
    &.hide{
        height: 0;
    }
    .capitalize{
        text-transform: capitalize;
    }
    .contact{
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        gap: 1rem;
        margin: 0 auto;
        color: white;
        min-height: 44px;
        font-size: 14px;
        a, div{
            display: flex;
            align-items: center;
            gap: .3rem;
        }
        svg{
            display: grid;
            place-items: center;
            font-size: 1.2rem;
        }
        @media screen and (max-width: 700px){
            font-size: 12px;
            svg{
                font-size: 1rem;
            }
        }
        @media screen and (max-width: 560px){
            .email{
                display: none;
            }
        }
    }
`;