import React from 'react'
import styled from 'styled-components'
import WavesPng from '../../assets/waves.png'

export default function Waves() {
  return (
    <StyledWaves>
        <img src={WavesPng} alt="" />
    </StyledWaves>
  )
}
const StyledWaves = styled.div`
    img{
        width: 100%;
        height: 250px;
    }
`;