import React from 'react'
import styled from 'styled-components'
import { CUBIC_BEZIER } from '../../constants';
import Container from '../utils/Container';

export default function Footer() {
  return (
    <StyledFooter>
        <Container>
            <p>Copyright &copy; 2022</p>
            <div className="used">
                <p>
                    <a href="https://github.com/akveo/eva-icons/" tabIndex='-1' target='_blank' rel='noreferrer'>Eva icons</a>
                </p>
                <p>
                    <a href="https://www.instagram.com/logancee/" tabIndex='-1' target='_blank' rel='noreferrer'>Design inspiration</a>
                </p>
                <p>
                    <a href="https://unsplash.com/photos/6sl88x150Xs"tabIndex='-1' target='_blank' rel='noreferrer'>Unsplash</a>
                </p>
                <p>
                    <a href="https://github.com/michalsnik/aos" tabIndex='-1' target='_blank' rel='noreferrer'>AOS</a>
                </p>
                <p>Developer: <a href="https://www.github.com/sebastianhaber/" tabIndex='-1' target='_blank' rel='noreferrer'>Sebastian Haber</a></p>
            </div>
        </Container>
    </StyledFooter>
  )
}
const StyledFooter = styled.footer`
    background-color: black;
    color: #666;
    padding: 2rem;
    text-align: center;
    .used{
        display: flex;
        flex-wrap: wrap;
        gap: 1rem;
        justify-content: center;
        margin-top: 1rem;
    }
    a{
        transition: color .2s ${CUBIC_BEZIER};
        &:hover{
            color: #999;
            text-decoration: underline;
        }
    }
`;