import { Icon } from '@iconify/react';
import React, { useState } from 'react'
import { Store } from 'react-notifications-component';
import styled from 'styled-components'
import { CUBIC_BEZIER, FORMSPARK_ACTION_URL } from '../../../constants';
import Container from '../../utils/Container';

const Input = ({id, required = false, type = 'text', label, onChange, value}) => {
    return (
        <StyledInput htmlFor={id}>
            <p className={required ? `required` : ``}>{label}</p>
            {type === 'textarea' ? 
                (<textarea id={id} required={required} onChange={onChange} value={value} autoComplete='off' />)
            : <input type={type} required={required} id={id} onChange={onChange} value={value} autoComplete='off' /> }
        </StyledInput>
    )
}
const INIT_VALUES = {
    email: '',
    name: '',
    phoneNumber: '',
    message: ''
}

export default function Kontakt({contact}) {
    const [values, setValues] = useState(INIT_VALUES)
    const [isLoading, setLoading] = useState(false);
    const onSubmit = async (e) => {
        e.preventDefault();
        setLoading(true);
        if(values.email.length < 5 || !values.message.length || !values.name.length) {
            return Store.addNotification({
                title: "Pflichtfelder sind leer!",
                type: "danger",
                insert: "top",
                container: "top-right",
                animationIn: ["animate__animated", "animate__fadeIn"],
                animationOut: ["animate__animated", "animate__fadeOut"],
                dismiss: {
                    duration: 5000,
                    onScreen: true
                }
            });
        }
        const message = `(${values.email} | ${values.name} ${values.phoneNumber && `| ${values.phoneNumber}`})
        ${values.message}`
        await fetch(FORMSPARK_ACTION_URL, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
            Accept: "application/json",
          },
          body: JSON.stringify({
            message,
          }),
        }).then(()=>{
            Store.addNotification({
                title: "Nachricht gesendet!",
                type: "success",
                insert: "top",
                container: "top-right",
                animationIn: ["animate__animated", "animate__fadeIn"],
                animationOut: ["animate__animated", "animate__fadeOut"],
                dismiss: {
                    duration: 5000,
                    onScreen: true
                }
            });
            setValues(INIT_VALUES)
        }).catch(()=>{
            Store.addNotification({
                title: ":(",
                message: "Beim Senden Ihrer Nachricht ist ein Fehler aufgetreten",
                type: "danger",
                insert: "top",
                container: "top-right",
                animationIn: ["animate__animated", "animate__fadeIn"],
                animationOut: ["animate__animated", "animate__fadeOut"],
                dismiss: {
                    duration: 5000,
                    onScreen: true
                }
            });
        });
        setLoading(false);
    }
    const firm = contact.firm?.replaceAll('\n', '<br />');
    return (
        <StyledKontakt id='kontakt'>
            <Container>
                <h2>Kontakt</h2>
                <div className="wrapper">
                    <form onSubmit={onSubmit} className="form">
                        <Input id='email' label='E-mail' required={true} type='email' value={values.email} onChange={(e)=>setValues({...values, email: e.target.value})} />
                        <Input id='name' label='Name' required={true} value={values.name} onChange={(e)=>setValues({...values, name: e.target.value})} />
                        <Input id='tel' label='Telefon' type='tel' value={values.phoneNumber} onChange={(e)=>setValues({...values, phoneNumber: e.target.value})} />
                        <Input id='message' label='Message' required={true} type='textarea' value={values.message} onChange={(e)=>setValues({...values, message: e.target.value})} />
                        <button className="btn btn-white" type='submit' disabled={isLoading}>
                            {isLoading ? <Icon icon="eva:refresh-outline" className='loading' /> 
                            : 'Formular senden'}
                        </button>
                    </form>
                    <div className="manual">
                        <Container>
                            <p><a href={`tel:${contact.phoneNumberDe}`}><Icon icon="eva:phone-outline" />{contact.phoneNumberDe}</a>(DE)</p>
                            <p><a href={`tel:${contact.phoneNumber}`}><Icon icon="eva:phone-outline" />{contact.phoneNumber}</a>(PL)</p>
                            <p><a href={`mailto:${contact.email}`}><Icon icon="eva:email-outline" />{contact.email}</a></p>
                            <p dangerouslySetInnerHTML={{__html: firm}}></p>
                        </Container>
                    </div>
                </div>
            </Container>
        </StyledKontakt>
    )
}
const StyledKontakt = styled.section`
    background-color: #5A25A5;
    padding: 2rem 2rem 6rem;
    color: white;
    h2{
        text-align: center;
        margin-top: 2rem;
    }
    .wrapper{
        display: flex;
        justify-content: space-around;
        flex-wrap: wrap-reverse;
        margin-top: 4rem;
        gap: 2rem;
    }
    .form{
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }
    .manual .container{
        display: flex;
        flex-direction: column;
        gap: 2rem;
        margin: 0 auto;
        color: white;
        height: 100%;
        p{
            position: relative;
            display: flex;
            gap: 0.2rem;
            width: fit-content;
        }
        a{
            display: flex;
            align-items: center;
            gap: .3rem;
            &::before{
                content: '';
                position: absolute;
                bottom: -0.3rem;
                left: 0;
                width: 0;
                height: 2px;
                background-color: white;
                transition: width .4s ${CUBIC_BEZIER};
            }
            &:hover::before{
                width: 100%;
            }
        }
        svg{
            display: grid;
            place-items: center;
            font-size: 1.5rem;
        }
    }
`;
const StyledInput = styled.label`
    input, textarea{
        border: 1px solid white;
        border-radius: .5rem;
        min-height: 2rem;
        background-color: transparent;
        outline: none;
        padding: 0.5rem 1rem;
        color: white;
        transition: border-color .2s ${CUBIC_BEZIER};
        &:focus{
            border-color: #D07F8E;
        }
    }
    textarea{
        resize: vertical;
        min-height: 8rem;
    }
    p.required::after{
        content: '*';
        margin-left: 0.2rem;
    }
`;