import React, { useEffect, useState } from 'react'
import styled from 'styled-components';
import Container from '../utils/Container';

export default function Service({data}) {
    const [message, setMessage] = useState("");
    const [transformedMessage, setTransformedMessage] = useState("");
    useEffect(() => {
        if(data) setMessage(data.replaceAll('\n', "<br />"));
    }, [data]);

    useEffect(()=>{
        let array = message.split("__");
        for(let i = 0; i<array.length; i++){
        if(i %2 !== 0){
            array[i] = `<b>${array[i]}</b>`
        }
        }
        setTransformedMessage(array.toString().replaceAll(",<b>", "<b>").replaceAll("</b>,", "</b>"))
    }, [message])
    return (
        <StyledContainer>
            <p dangerouslySetInnerHTML={{__html: transformedMessage}} data-aos="fade-in" data-aos-delay="800"></p>
        </StyledContainer>
    )
}
const StyledContainer = styled(Container)`
    padding-top: 4rem;
`;